/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'
import GLightbox from 'glightbox'
import Img from 'react-image'
import Spinner from "react-spinners/RingLoader"
import resolveImage from './resolveImage'

const lightbox = GLightbox({
    touchNavigation: true,
    loop: true,
    autoplayVideos: true,
    onOpen: () => { },
    beforeSlideLoad: (slide, data) => { }
});

function LocalPicture(app, feature, index, openLb) {
    const aosList = [
        "slide-up",
        "flip-left",
        "zoom-in",
        "flip-right"
    ]
    const shadowCss = feature.shadow ? css`
            border-radius: 5px;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            overflow: hidden;

            width: 100%;
            height: auto;
        ` : css`
            max-height: 90vh;
            max-width: 100%;
        `

    const aos = aosList[index % aosList.length]

    return (
        <div css={css`
            
        `} data-aos={aos}>
            <div>
                <a
                    href='#feature'
                    className="glightbox"
                    onClick={openLb}
                >
                    <img
                        css={shadowCss}
                        src={resolveImage(app, feature)}
                        alt={feature.image}
                        loader={
                            <Spinner
                                size={30}
                                color={"#27CBC0"}
                                loading={true}
                            />
                        }
                    />
                </a>
            </div>
        </div>
    )
}

const RemotePicture = (feature) => {
    return (
        <div css={css`
            align: center;
        `}>
            <Img css={css`
                border-radius: 5px;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                overflow: hidden;
                max-width: 98%;
            `}
                src={feature.imageUrl}
                alt={feature.imageUrl}
                loader={
                    <Spinner
                        size={30}
                        color={"#27CBC0"}
                        loading={true}
                    />
                }
            />
        </div>
    )
}

export default function Features(props) {
    const app = props.app
    const features = app.features

    const lbElements = features
        .filter((feature) => { return feature.image !== undefined })
        .map((feature) => {
            return {
                'href': resolveImage(app, feature),
                'type': 'image',
                'title': feature.title,
                'description': feature.texts[0],
            }
        })

    const openLb = () => {
        lightbox.setElements(lbElements)
        lightbox.open()
    }

    let list = features.map((feature, index) => {
        const texts = feature.texts.map((text) => {
            return (
                <span css={css`
                    diplay: inline-block;
                    margin: 6px;
                    color: #2E2E2E;
                    font-weight: 600;
                    font-size: 1.2em;
                    line-height: 1.5em;
                `}
                    key={text}
                >
                    <i className='material-icons-round' css={css`
                        margin-right: 0.3em;
                        position: relative;
                        top: 0.2em;
                        color: green;
                    `}>check_circle</i>
                    {text}
                </span>
            )
        })

        const Picture = () => {
            if (feature.imageUrl !== undefined) {
                return RemotePicture(feature)
            } else {
                return LocalPicture(app, feature, index, openLb)
            }
        }

        const Content = () => (
            <div css={css`
                display: grid;
            `} data-aoes="fade-up">
                <div css={css`
                    display: flex;
                    align-items: center;
                    position: relative;
                `}>
                    <p css={css`
                        font-weight: bold;
                        font-size: 2em;
                        margin-left: 5px;
                        display: inline;
                        background: linear-gradient(180deg, rgba(255,255,255,0) 65%, #FFD0AE 65%);
                    `}>
                        {feature.title}
                    </p>
                </div>
                {texts}
            </div>
        )

        const Left = (index % 2 == 0) ? <Picture /> : <Content />
        const Right = (index % 2 == 0) ? <Content /> : <Picture />

        return (
            <div css={css`
                display: grid;
                grid-template-columns: repeat(2, minmax(350px, 1fr));
                grid-gap: 8vw;
                align-items: center;
                margin-top: 100px;
                margin-bottom: 50px;
                min-height: 50vh;
                padding: 0 5vw;

                @media (max-width: 500px) {
                    grid-template-columns: 1fr; 
                }
            `}
                key={index}
            >
                <div>
                    {Left}
                </div>
                <div>
                    {Right}
                </div>
            </div>
        )
    })

    return (
        <div css={css`
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
        `}>
            <div css={css`
                margin: 0 auto;
            `}>
                <p class='section-title'>Features</p>
            </div>
            {list}
        </div>
    )
}